const child_process = require('child_process');

/**
 * run shell
 *
 * @param command
 * @param options
 * @returns {Promise}
 */
export async function runShell(command, options) {
    return new Promise(function (resolve, reject) {
        let init = child_process.spawn(command, options, {'stdio': 'inherit'});
        init.on('exit', function (code) {
            resolve(code);
        });
    });
}

/**
 * npm install dependence
 *
 * @param command
 * @param options
 * @returns {Promise}
 */
export async function installDependence(options) {
    return await runShell('npm',options);
}
